$_Primary: #81c754;
$_PrimaryDark: #276D00;
$_Secondary: #5481c7;
$_SecondaryDark: #2e528b;
$_Error: #B00020;
$_BoxShadow: 0px 4px 6px #9792b8;

$_GreyScale1: #f5f4f7;
$_GreyScale2: darken($_GreyScale1, 10%);
$_GreyScale3: darken($_GreyScale1, 20%);
