$_GreyScale5: darken($_GreyScale1, 50%);
$_GreyScale6: darken($_GreyScale1, 80%);


$background: $_GreyScale3;
$container: $_GreyScale1;
$container-shadow: $_BoxShadow;

$text: $_GreyScale6;

$link: $_Secondary;
