$button-primary-border-focus: $_PrimaryDark;

$button-secondary-text: $_Secondary;
$button-secondary-background: $_GreyScale1;
$button-secondary-border: $_Secondary;

$button-secondary-text-focus: $_SecondaryDark;
$button-secondary-background-focus: $_GreyScale1;
$button-secondary-border-focus: $_SecondaryDark;

$button-disabled-text: $_GreyScale6;
$button-disabled-background: $_GreyScale3;
$button-disabled-border: $_GreyScale3;

$checkbox-background: $_GreyScale1;
$checkbox-border: $_GreyScale4;
$checkbox-check: $_Primary;
